.list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 280px;
}

.list-item {
  background-color: white;
  border: 1px solid red;
  cursor: pointer;
  margin: 0;
  padding: 10px;
  text-align: center;
  width: 100%;
}

.list-item:hover,
.list-item:active {
  background-color: #ccc;
}